import React, { Component } from 'react';
import Aux from '../../hoc/Aux';
import '../Subjects/students.css';
import prof5 from '../../images/man-in-black-polo-standing-in-front-of-a-class-3981797.jpg';

const alertAdded = ( props ) => (
    alert("All students have been added.")
)

class dSLab extends Component{
    state = {
        dataStrLab: [
            {name: 'name1', roll:'roll no.1'},
            {name: 'name2', roll:'roll no.2'},
            {name: 'name3', roll:'roll no.3'},
            {name: 'name4', roll:'roll no.4'},
            {name: 'name5', roll:'roll no.5'},
            {name: 'name6', roll:'roll no.6'},
        ],
        showDSLab0: true,
        showDSLab1: true,
        showDSLab2: true,
        showDSLab3: true,
        showDSLab4: true,
        showDSLab5: true,
    }

    switchNameHandler0 = () => {
        this.setState({showDSLab0: false});
        alert(this.state.dataStrLab[0].name+' was removed')
    }

    switchNameHandler1 = () => {
        this.setState({showDSLab1: false});
        alert(this.state.dataStrLab[1].name+' was removed')
    }
    
    switchNameHandler2 = () => {
        this.setState({showDSLab2: false});
        alert(this.state.dataStrLab[2].name+' was removed')
    }
    switchNameHandler3 = () => {
        this.setState({showDSLab3: false});
        alert(this.state.dataStrLab[3].name+' was removed')
    }
    switchNameHandler4 = () => {
        this.setState({showDSLab4: false});
        alert(this.state.dataStrLab[4].name+' was removed')
    }
    switchNameHandler5 = () => {
        this.setState({showDSLab5: false});
        alert(this.state.dataStrLab[5].name+' was removed')
    }
    render(){
        return (
            <Aux>
                <div className='s-prof'>
                    <img src={prof5} className='prof-img'/>
                    <p>Dr. John Doe</p>
                </div>
                <div className='s-head'>
                    <p className='subject-heading'>Welcomes</p>
                    <p className='subject-heading'>To</p>
                    <p className='subject-heading'><b>Applied Mathematics</b> Class!!</p>
                </div>
                <div className='sub-students'>
                    <p className='students-head'>STUDENTS LIST</p>
                    <div className='students-list'>
                        <ul>
                            
                            <li>
                                <p className='roll table-head'><b>Roll No.</b></p>
                                <p className='name table-head'><b>Name</b></p>
                                <p className='remove table-head'><b>Remove</b></p>
                            </li>
                            { this.state.showDSLab0 ?
                            <li>
                                <p className='roll'>{this.state.dataStrLab[0].roll}</p>
                                <p className='name'>{this.state.dataStrLab[0].name}</p>
                                <p className='remove'><button className='remove-btn' onClick={this.switchNameHandler0}>Remove</button></p>
                            </li> : null}
                            
                            { this.state.showDSLab1 ?
                            <li>
                                <p className='roll'>{this.state.dataStrLab[1].roll}</p>
                                <p className='name'>{this.state.dataStrLab[1].name}</p>
                                <p className='remove'><button className='remove-btn' onClick={this.switchNameHandler1}>Remove</button></p>
                            </li> : null}
                            
                            { this.state.showDSLab2 ?
                            <li>
                                <p className='roll'>{this.state.dataStrLab[2].roll}</p>
                                <p className='name'>{this.state.dataStrLab[2].name}</p>
                                <p className='remove'><button className='remove-btn' onClick={this.switchNameHandler2}>Remove</button></p>
                            </li> : null}
                            
                            { this.state.showDSLab3 ?
                            <li>
                                <p className='roll'>{this.state.dataStrLab[3].roll}</p>
                                <p className='name'>{this.state.dataStrLab[3].name}</p>
                                <p className='remove'><button className='remove-btn' onClick={this.switchNameHandler3}>Remove</button></p>
                            </li> : null}

                            { this.state.showDSLab4 ?
                            <li>
                                <p className='roll'>{this.state.dataStrLab[4].roll}</p>
                                <p className='name'>{this.state.dataStrLab[4].name}</p>
                                <p className='remove'><button className='remove-btn' onClick={this.switchNameHandler4}>Remove</button></p>
                            </li> : null}
                            
                            { this.state.showDSLab5 ?
                            <li>
                                <p className='roll'>{this.state.dataStrLab[5].roll}</p>
                                <p className='name'>{this.state.dataStrLab[5].name}</p>
                                <p className='remove'><button className='remove-btn' onClick={this.switchNameHandler5}>Remove</button></p>
                            </li> : null}
                        </ul>
                    </div>
        
                    <button onClick={alertAdded} className='add-btn'>Add All Participants</button>
                </div>
        
            </Aux>
        );
    }
}

export default dSLab;