import React from 'react';
import Aux from '../../hoc/Aux';
import './HomeContent.css';
import AboutUs from '../HomeContent/AboutUs';
import Professors from '../HomeContent/Professors';
import FooterContent from './Footer';
import { Route } from 'react-router-dom';
import Subjects from '../Subjects/subjects';
import Mathematics from '../Subjects/appliedMathematics';
import Algorithm from '../Subjects/algorithm';
import AlgorithmLab from '../Subjects/algorithm-lab';
import DataStructure from '../Subjects/data-stucture';
import DataStructureLab from '../Subjects/data-str-lab';

const homeContent = ( props ) => (
    <Aux>
        

        <Route path="/" exact render={() =>
            <div>
                <div className='intro'>
                    <p>EDUCATION</p>
                    <p>IS</p>
                    <p>EMPOWERMENT</p>
                </div>

                <div className='intro-text'>
                    <p>NEED TO KNOW THE ONES' GOING TO HELP YOU OUT?? </p>
                </div>

                <div className='lm-btn'>
                    <a href=".Professors" className='learn-more'>LEARN MORE</a>
                </div>
                <AboutUs/>
                <Professors/>
            </div>
        }/>

        <Route path="/subjects" exact render={() =>
            <Subjects/>
        } />
    
        <Route path="/subjects/applied-mathematics" exact render={() =>
            <Mathematics/>
        } />

        <Route path="/subjects/algorithm" exact render={() =>
            <Algorithm/>
        } />

        <Route path="/subjects/algorithm-lab" exact render={() =>
            <AlgorithmLab/>
        } />

        <Route path="/subjects/data-structure" exact render={() =>
            <DataStructure/>
        } />

        <Route path="/subjects/data-structure-lab" exact render={() =>
            <DataStructureLab/>
        } />

        <FooterContent/>
        
    </Aux>
);

export default homeContent;