import React, { Component } from 'react';
import Aux from '../../hoc/Aux';
import prof4 from '../../images/photo-of-woman-teaching-935943.jpg';

const alertAdded = ( props ) => (
    alert("All students have been added.")
)

class dataStructure extends Component{
    state = {
        dataStr: [
            {name: 'name1', roll:'roll no.1'},
            {name: 'name2', roll:'roll no.2'},
            {name: 'name3', roll:'roll no.3'},
            {name: 'name4', roll:'roll no.4'},
            {name: 'name5', roll:'roll no.5'},
            {name: 'name6', roll:'roll no.6'},
        ],
        showDataStr0: true,
        showDataStr1: true,
        showDataStr2: true,
        showDataStr3: true,
        showDataStr4: true,
        showDataStr5: true,
    }

    switchNameHandler0 = () => {
        this.setState({showDataStr0: false});
        alert(this.state.dataStr[0].name+' was removed')
    }

    switchNameHandler1 = () => {
        this.setState({showDataStr1: false});
        alert(this.state.dataStr[1].name+' was removed')
    }
    
    switchNameHandler2 = () => {
        this.setState({showDataStr2: false});
        alert(this.state.dataStr[2].name+' was removed')
    }
    switchNameHandler3 = () => {
        this.setState({showDataStr3: false});
        alert(this.state.dataStr[3].name+' was removed')
    }
    switchNameHandler4 = () => {
        this.setState({showDataStr4: false});
        alert(this.state.dataStr[4].name+' was removed')
    }
    switchNameHandler5 = () => {
        this.setState({showDataStr5: false});
        alert(this.state.dataStr[5].name+' was removed')
    }
    render(){
        return (
            <Aux>
                <div className='s-prof'>
                    <img src={prof4} className='prof-img'/>
                    <p>Dr. Isabelle Turner</p>
                </div>
                <div className='s-head'>
                    <p className='subject-heading'>Welcomes</p>
                    <p className='subject-heading'>To</p>
                    <p className='subject-heading'><b>Applied Mathematics</b> Class!!</p>
                </div>
                <div className='sub-students'>
                    <p className='students-head'>STUDENTS LIST</p>
                    <div className='students-list'>
                        <ul>
                            
                            <li>
                                <p className='roll table-head'><b>Roll No.</b></p>
                                <p className='name table-head'><b>Name</b></p>
                                <p className='remove table-head'><b>Remove</b></p>
                            </li>
                            { this.state.showDataStr0 ?
                            <li>
                                <p className='roll'>{this.state.dataStr[0].roll}</p>
                                <p className='name'>{this.state.dataStr[0].name}</p>
                                <p className='remove'><button className='remove-btn' onClick={this.switchNameHandler0}>Remove</button></p>
                            </li> : null}
                            
                            { this.state.showDataStr1 ?
                            <li>
                                <p className='roll'>{this.state.dataStr[1].roll}</p>
                                <p className='name'>{this.state.dataStr[1].name}</p>
                                <p className='remove'><button className='remove-btn' onClick={this.switchNameHandler1}>Remove</button></p>
                            </li> : null}
                            
                            { this.state.showDataStr2 ?
                            <li>
                                <p className='roll'>{this.state.dataStr[2].roll}</p>
                                <p className='name'>{this.state.dataStr[2].name}</p>
                                <p className='remove'><button className='remove-btn' onClick={this.switchNameHandler2}>Remove</button></p>
                            </li> : null}
                            
                            { this.state.showDataStr3 ?
                            <li>
                                <p className='roll'>{this.state.dataStr[3].roll}</p>
                                <p className='name'>{this.state.dataStr[3].name}</p>
                                <p className='remove'><button className='remove-btn' onClick={this.switchNameHandler3}>Remove</button></p>
                            </li> : null}

                            { this.state.showDataStr4 ?
                            <li>
                                <p className='roll'>{this.state.dataStr[4].roll}</p>
                                <p className='name'>{this.state.dataStr[4].name}</p>
                                <p className='remove'><button className='remove-btn' onClick={this.switchNameHandler4}>Remove</button></p>
                            </li> : null}
                            
                            { this.state.showDataStr5 ?
                            <li>
                                <p className='roll'>{this.state.dataStr[5].roll}</p>
                                <p className='name'>{this.state.dataStr[5].name}</p>
                                <p className='remove'><button className='remove-btn' onClick={this.switchNameHandler5}>Remove</button></p>
                            </li> : null}
                        </ul>
                    </div>
        
                    <button onClick={alertAdded} className='add-btn'>Add All Participants</button>
                </div>
        
            </Aux>
        );
    }
}

export default dataStructure;