import React, { Component } from 'react';
import Aux from '../../hoc/Aux';
import '../Subjects/students.css';
import prof1 from '../../images/happy-adult-woman-reading-book-with-interest-on-street-3772711.jpg';

const alertAdded = ( props ) => (
    alert("All students have been added.")
)

class Mathematics extends Component {
    state = {
        maths: [
            {name: 'name1', roll:'roll no.1'},
            {name: 'name2', roll:'roll no.2'},
            {name: 'name3', roll:'roll no.3'},
            {name: 'name4', roll:'roll no.4'},
            {name: 'name5', roll:'roll no.5'},
            {name: 'name6', roll:'roll no.6'},
        ],
        showMaths0: true,
        showMaths1: true,
        showMaths2: true,
        showMaths3: true,
        showMaths4: true,
        showMaths5: true,
    }

    switchNameHandler0 = () => {
        this.setState({showMaths0: false});
        alert(this.state.maths[0].name+' was removed')
    }

    switchNameHandler1 = () => {
        this.setState({showMaths1: false});
        alert(this.state.maths[1].name+' was removed')
    }
    
    switchNameHandler2 = () => {
        this.setState({showMaths2: false});
        alert(this.state.maths[2].name+' was removed')
    }
    switchNameHandler3 = () => {
        this.setState({showMaths3: false});
        alert(this.state.maths[3].name+' was removed')
    }
    switchNameHandler4 = () => {
        this.setState({showMaths4: false});
        alert(this.state.maths[4].name+' was removed')
    }
    switchNameHandler5 = () => {
        this.setState({showMaths5: false});
        alert(this.state.maths[5].name+' was removed')
    }
    render(){
        return (
            <Aux>
                <div className='s-prof'>
                    <img src={prof1} className='prof-img'/>
                    <p>Professor Sophia Wood </p>
                </div>
                <div className='s-head'>
                    <p className='subject-heading'>Welcomes</p>
                    <p className='subject-heading'>To</p>
                    <p className='subject-heading'><b>Applied Mathematics</b> Class!!</p>
                </div>
                <div className='sub-students'>
                    <p className='students-head'>STUDENTS LIST</p>
                    <div className='students-list'>
                        <ul>
                            
                            <li>
                                <p className='roll table-head'><b>Roll No.</b></p>
                                <p className='name table-head'><b>Name</b></p>
                                <p className='remove table-head'><b>Remove</b></p>
                            </li>
                            { this.state.showMaths0 ?
                            <li>
                                <p className='roll'>{this.state.maths[0].roll}</p>
                                <p className='name'>{this.state.maths[0].name}</p>
                                <p className='remove' onClick={this.switchNameHandler0}><button className='remove-btn'>Remove</button></p>
                            </li> : null}
                            
                            { this.state.showMaths1 ?
                            <li>
                                <p className='roll'>{this.state.maths[1].roll}</p>
                                <p className='name'>{this.state.maths[1].name}</p>
                                <p className='remove' onClick={this.switchNameHandler1}><button className='remove-btn'>Remove</button></p>
                            </li> : null}
                            
                            { this.state.showMaths2 ?
                            <li>
                                <p className='roll'>{this.state.maths[2].roll}</p>
                                <p className='name'>{this.state.maths[2].name}</p>
                                <p className='remove' onClick={this.switchNameHandler2}><button className='remove-btn'>Remove</button></p>
                            </li> : null}
                            
                            { this.state.showMaths3 ?
                            <li>
                                <p className='roll'>{this.state.maths[3].roll}</p>
                                <p className='name'>{this.state.maths[3].name}</p>
                                <p className='remove' onClick={this.switchNameHandler3}><button className='remove-btn'>Remove</button></p>
                            </li> : null}

                            { this.state.showMaths4 ?
                            <li>
                                <p className='roll'>{this.state.maths[4].roll}</p>
                                <p className='name'>{this.state.maths[4].name}</p>
                                <p className='remove' onClick={this.switchNameHandler4}><button className='remove-btn'>Remove</button></p>
                            </li> : null}
                            
                            { this.state.showMaths5 ?
                            <li>
                                <p className='roll'>{this.state.maths[5].roll}</p>
                                <p className='name'>{this.state.maths[5].name}</p>
                                <p className='remove'><button className='remove-btn' onClick={this.switchNameHandler5}>Remove</button></p>
                            </li> : null}
                        </ul>
                    </div>
        
                    <button onClick={alertAdded} className='add-btn'>Add All Participants</button>
                </div>
        
            </Aux>
        );
    }
}

export default Mathematics;