import React, { Component } from 'react';
import Aux from '../../hoc/Aux';
import '../Subjects/students.css';
import prof2 from '../../images/man-in-beige-blazer-holding-tablet-computer-3184328.jpg';
const alertAdded = ( props ) => (
    alert("All students have been added.")
)

class algorithm extends Component{
    state = {
        algo: [
            {name: 'name1', roll:'roll no.1'},
            {name: 'name2', roll:'roll no.2'},
            {name: 'name3', roll:'roll no.3'},
            {name: 'name4', roll:'roll no.4'},
            {name: 'name5', roll:'roll no.5'},
            {name: 'name6', roll:'roll no.6'},
        ],
        showAlgo0: true,
        showAlgo1: true,
        showAlgo2: true,
        showAlgo3: true,
        showAlgo4: true,
        showAlgo5: true,
    }

    switchNameHandler0 = () => {
        this.setState({showAlgo0: false});
        alert(this.state.algo[0].name+' was removed')
    }

    switchNameHandler1 = () => {
        this.setState({showAlgo1: false});
        alert(this.state.algo[1].name+' was removed')
    }
    
    switchNameHandler2 = () => {
        this.setState({showAlgo2: false});
        alert(this.state.algo[2].name+' was removed')
    }
    switchNameHandler3 = () => {
        this.setState({showAlgo3: false});
        alert(this.state.algo[3].name+' was removed')
    }
    switchNameHandler4 = () => {
        this.setState({showAlgo4: false});
        alert(this.state.algo[4].name+' was removed')
    }
    switchNameHandler5 = () => {
        this.setState({showAlgo5: false});
        alert(this.state.algo[5].name+' was removed')
    }
    render(){
        return (
            <Aux>
                <div className='s-prof'>
                    <img src={prof2} className='prof-img'/>
                    <p>Dr. James Amber</p>
                </div>
                <div className='s-head'>
                    <p className='subject-heading'>Welcomes</p>
                    <p className='subject-heading'>To</p>
                    <p className='subject-heading'><b>Applied Mathematics</b> Class!!</p>
                </div>
                <div className='sub-students'>
                    <p className='students-head'>STUDENTS LIST</p>
                    <div className='students-list'>
                        <ul>
                            
                            <li>
                                <p className='roll table-head'><b>Roll No.</b></p>
                                <p className='name table-head'><b>Name</b></p>
                                <p className='remove table-head'><b>Remove</b></p>
                            </li>
                            { this.state.showAlgo0 ?
                            <li>
                                <p className='roll'>{this.state.algo[0].roll}</p>
                                <p className='name'>{this.state.algo[0].name}</p>
                                <p className='remove'><button className='remove-btn' onClick={this.switchNameHandler0}>Remove</button></p>
                            </li> : null}
                            
                            { this.state.showAlgo1 ?
                            <li>
                                <p className='roll'>{this.state.algo[1].roll}</p>
                                <p className='name'>{this.state.algo[1].name}</p>
                                <p className='remove'><button className='remove-btn' onClick={this.switchNameHandler1}>Remove</button></p>
                            </li> : null}
                            
                            { this.state.showAlgo2 ?
                            <li>
                                <p className='roll'>{this.state.algo[2].roll}</p>
                                <p className='name'>{this.state.algo[2].name}</p>
                                <p className='remove'><button className='remove-btn' onClick={this.switchNameHandler2}>Remove</button></p>
                            </li> : null}
                            
                            { this.state.showAlgo3 ?
                            <li>
                                <p className='roll'>{this.state.algo[3].roll}</p>
                                <p className='name'>{this.state.algo[3].name}</p>
                                <p className='remove'><button className='remove-btn' onClick={this.switchNameHandler3}>Remove</button></p>
                            </li> : null}

                            { this.state.showAlgo4 ?
                            <li>
                                <p className='roll'>{this.state.algo[4].roll}</p>
                                <p className='name'>{this.state.algo[4].name}</p>
                                <p className='remove'><button className='remove-btn' onClick={this.switchNameHandler4}>Remove</button></p>
                            </li> : null}
                            
                            { this.state.showAlgo5 ?
                            <li>
                                <p className='roll'>{this.state.algo[5].roll}</p>
                                <p className='name'>{this.state.algo[5].name}</p>
                                <p className='remove'><button className='remove-btn' onClick={this.switchNameHandler5}>Remove</button></p>
                            </li> : null}
                        </ul>
                    </div>
        
                    <button onClick={alertAdded} className='add-btn'>Add All Participants</button>
                </div>
        
            </Aux>
        );
    }
}

export default algorithm;