import React from 'react';
import Aux from '../../hoc/Aux';
import './footer.css';
import favicon from '../../images/favicon.ico';

const footer = ( props ) => (
    <Aux>
        <div className='footer-str'>
            <div className='footer-content'>
                <div>
                    <img className='favicon' src={favicon}/>
                    <p>TA-APP</p>
                </div>
                <div>
                    @Copyright 2020-2021
                </div>
                </div>
        </div>
    </Aux>
);

export default footer;