import React, { Component } from 'react';
import Aux from '../../hoc/Aux';
import '../Subjects/students.css';
import prof3 from '../../images/man-beside-flat-screen-television-with-photos-background-716276.jpg';
const alertAdded = ( props ) => (
    alert("All students have been added.")
)

class algorithmLab extends Component{
    state = {
        algoLab: [
            {name: 'name1', roll:'roll no.1'},
            {name: 'name2', roll:'roll no.2'},
            {name: 'name3', roll:'roll no.3'},
            {name: 'name4', roll:'roll no.4'},
            {name: 'name5', roll:'roll no.5'},
            {name: 'name6', roll:'roll no.6'},
        ],
        showAlgoLab0: true,
        showAlgoLab1: true,
        showAlgoLab2: true,
        showAlgoLab3: true,
        showAlgoLab4: true,
        showAlgoLab5: true,
    }

    switchNameHandler0 = () => {
        this.setState({showAlgoLab0: false});
        alert(this.state.algoLab[0].name+' was removed')
    }

    switchNameHandler1 = () => {
        this.setState({showAlgoLab1: false});
        alert(this.state.algoLab[1].name+' was removed')
    }
    
    switchNameHandler2 = () => {
        this.setState({showAlgoLab2: false});
        alert(this.state.algoLab[2].name+' was removed')
    }
    switchNameHandler3 = () => {
        this.setState({showAlgoLab3: false});
        alert(this.state.algoLab[3].name+' was removed')
    }
    switchNameHandler4 = () => {
        this.setState({showAlgoLab4: false});
        alert(this.state.algoLab[4].name+' was removed')
    }
    switchNameHandler5 = () => {
        this.setState({showAlgoLab5: false});
        alert(this.state.algoLab[5].name+' was removed')
    }
    render(){
        return (
            <Aux>
                <div className='s-prof'>
                    <img src={prof3} className='prof-img'/>
                    <p>Professor Oliver Twain</p>
                </div>
                <div className='s-head'>
                    <p className='subject-heading'>Welcomes</p>
                    <p className='subject-heading'>To</p>
                    <p className='subject-heading'><b>Applied Mathematics</b> Class!!</p>
                </div>
                <div className='sub-students'>
                    <p className='students-head'>STUDENTS LIST</p>
                    <div className='students-list'>
                        <ul>
                            
                            <li>
                                <p className='roll table-head'><b>Roll No.</b></p>
                                <p className='name table-head'><b>Name</b></p>
                                <p className='remove table-head'><b>Remove</b></p>
                            </li>
                            { this.state.showAlgoLab0 ?
                            <li>
                                <p className='roll'>{this.state.algoLab[0].roll}</p>
                                <p className='name'>{this.state.algoLab[0].name}</p>
                                <p className='remove'><button className='remove-btn' onClick={this.switchNameHandler0}>Remove</button></p>
                            </li> : null}
                            
                            { this.state.showAlgoLab1 ?
                            <li>
                                <p className='roll'>{this.state.algoLab[1].roll}</p>
                                <p className='name'>{this.state.algoLab[1].name}</p>
                                <p className='remove'><button className='remove-btn' onClick={this.switchNameHandler1}>Remove</button></p>
                            </li> : null}
                            
                            { this.state.showAlgoLab2 ?
                            <li>
                                <p className='roll'>{this.state.algoLab[2].roll}</p>
                                <p className='name'>{this.state.algoLab[2].name}</p>
                                <p className='remove'><button className='remove-btn' onClick={this.switchNameHandler2}>Remove</button></p>
                            </li> : null}
                            
                            { this.state.showAlgoLab3 ?
                            <li>
                                <p className='roll'>{this.state.algoLab[3].roll}</p>
                                <p className='name'>{this.state.algoLab[3].name}</p>
                                <p className='remove'><button className='remove-btn' onClick={this.switchNameHandler3}>Remove</button></p>
                            </li> : null}

                            { this.state.showAlgoLab4 ?
                            <li>
                                <p className='roll'>{this.state.algoLab[4].roll}</p>
                                <p className='name'>{this.state.algoLab[4].name}</p>
                                <p className='remove'><button className='remove-btn' onClick={this.switchNameHandler4}>Remove</button></p>
                            </li> : null}
                            
                            { this.state.showAlgoLab5 ?
                            <li>
                                <p className='roll'>{this.state.algoLab[5].roll}</p>
                                <p className='name'>{this.state.algoLab[5].name}</p>
                                <p className='remove'><button className='remove-btn' onClick={this.switchNameHandler5}>Remove</button></p>
                            </li> : null}
                        </ul>
                    </div>
        
                    <button onClick={alertAdded} className='add-btn'>Add All Participants</button>
                </div>
        
            </Aux>
        );
    }
}

export default algorithmLab;